/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"jumbotron\">\r\n      <h3>The Feast of Abune Gebre Menfes Qidus </h3>\r\n      <h5>Decenber 12, 2019</h5>\r\n      <p>Abune Gebre Menfes Qidus [means the salve of the Holy Spirit] was born on Tahisas 29 in 829 E.C; his date of birth and conception coincided with the day of His GOD, JESUS CHRIST. </p><p>His fatherâs name was Simeon, and his motherâs name was âAklesya who were righteous before GOD.  </p><p>\r\n\r\n        They remained childless in moan and prayer for a period of thirty years because they had not got a son. However, later God heard their prayer and gave them this child.\r\n      According to his Gadl (Hagiography), the saint was appeared from the city of Nehisa, in Egypt and lived there 300 years in the desert of Egypt. Then, he came to Ethiopia by the order of God and wandered about the wildernesses of Ethiopia.   He, then, departed to the land of Ziquala, that later on became the great monastery of the saint and lived there 265 years as if naked and drinking no water and eating no food, exceeding many prophets of old and unlike a man upon earth, but likened to the angels.\r\n       </p>\r\n       <p>So his holy life began and was accomplished, now on the third year of his birth, GOD commanded the angel Gabriel and said unto him, âGo to the house of Simeon, and take the child from the breast of his mother, and bring him into the desert where there are many monks, and lay him down in their courtyard.â And say to the abbot, âTake the child from the courtyard, and bring him into the sanctuaryâ\r\n        Due to this divine order, the abbot raised him according to the Churchâs rule, teaching all the scriptures and other religious learning. Later, he got ordination from the bishop called Abba Abraham and he dwelt in the Egyptian desert called âGebotaâ [most probably in Upper Egypt] three hundred years.\r\n        Later on, he came to Ethiopia via Axum in 1129 E.C. during the reign of Qidus Harbey (also called Gebre Mariam), and expanded evangelistic activities in north and central Ethiopia. Then, he went to the next reigning king âLalibelaâ, said to be in 1168 E.C., to discuss church issues. King Lalibela was excited and bowed to the saint many times and requested of him to stay with him.  But the saint did not accept the kingâs request, instead he promised the king saying, âI will live in mount Ziquala and I will not depart from your country.â\r\n        He is one of the most renowned Saints in Ethiopia along with St. Tekle Haymanot in evangelistic activities and is known as the founder of Zequala monastery.He is known by performing incredible miracles throughout his age and afterwards and is recognized by his exceptional quality, here are some of his miracles;\r\n        </p><p> on the third day of his birth, the child rose up, and came down from his motherâs breast, and he stood up and bowed three times to the FATHER, the SON, and the HOLY SPIRIT, and who also said, âGlory be to the FATHER, Glory be to the SON, Glory be to the HOLY SPIRIT, Who has brought me out of the darkness into the light.â\r\n        The saint did not eat the bread of earth, or drink water, or wear raiment and he never prepared anything for his body, but he lived naked in the desert. However, he was arrayed by his beard and the hair of his head that covered his whole body like a thatch and swept the ground.  He was like the fowls of heaven, for he thought nothing about the food of this world, but he hungered and thirsted for God; his sustenance was offered from heaven.\r\n         </p><p>One day, the three saints (Abba Samuel of Waldebba, Abba Anbes of the land of Hazalo and Abba Benyam of the upper land of Egypt) came to the land of Kabd, carried on their lions, to ask and see Abune Gebre Menfes Qidus âs celebrity. When they reached there they couldnât get him and they stayed there for seven days in search of him.  Then a certain lion came to the three saints and devoured their lions. Then, the saints felt bitterly sad, and their sorrow was revealed unto our father, Abune Gebre Menfes Qidus.  Then after he came to them with the great power accompanied by his lions and leopards  , the saint rebuked the lions, and he rose up the dead lions from the belly of the lions and sent them away to their former owners.\r\n        During his combat, Satan came to him in the form of a black raven, pecked at his eyes, and dug out his eyeballs. Consequently, the saint remained blind for ninety-eight years, till the angles (Michael and Gabriel) healed his eyes through their breathing.\r\n          </p><p>He lived 100 years in solitude in Lake Ziquala, praying for Ethiopia and the world as well. He lived in the desert escorted by 60 lions and 60 tigers that served him. The saint lived 300 years in Egypt and 262 in Ethiopia with a total life span of 562 years on earth and died on Megabit 5 /March 13. Due to his spiritual struggle, he was entitled with these names: âthe Star of the desertâ and âthe Head of Anchoritesâ.\r\n        The Ethiopian church commemorates the feast of the saint on the 5th date of every Ethiopian month, of which Megabit 5 /March 13 is the day of the death of the saint), and Tikmit 5 /October 15 is the great annual feast of the saint. The reason for his commemoration on the 5th date of every Ethiopian month is:\r\n        The entry of the saint from Egypt to Ethiopia; the day on which he went out from the lake where he received the covenant from God; the day on which he raised the dead lions; the first mass of his church built in his name was also celebrated on this day.  His departure is commemorated on Megabit 5/March 13.  Consequently his Gadl states that, he died in Ziquala and rested in Medre Kebd.\r\n                     </p><p>May the prayer of Abune Gebre Menfes Qidus be up on us!\r\n         <p>Source:</p><p>\r\n        â¢ ááµá áá¥á¨ ááááµ áá±áµ : 1992 E.C :: (The Hagiography of Abune Gebre Menfes Kidus) 1992 E.C.\r\n        â¢ Synaxarium:The book of saints of the Ethiopian Church\r\n        â¢ á¨ááá á°á¥á¨ á¨áá­á¥áµ á á¡á áá¥á¨ ááááµ áá±áµ áá³á ááá³á áá½ááµ á¥áááµ: 2007::  (Annual Journal of Ziquala Debre Kewakibt Abune Gebre Menfes Kidus, October, 2007).\r\n        </p>\r\n\r\n\r\n  </div>\r\n");

